import React from 'react';
import { string } from 'prop-types';

import { ReactComponent as Hide } from 'assets/icons/hide.svg';
import { ReactComponent as Lock } from 'assets/icons/lock.svg';
import { ReactComponent as Letter } from 'assets/icons/letter.svg';
import { ReactComponent as Show } from 'assets/icons/show.svg';

export default function Icon({ shape, className, toggleVisible }) {
  const combineClass = `${className}`;
  let Icon = null;
  switch (shape) {
    case 'letter':
      Icon = Letter;
      break;
    case 'lock':
      Icon = Lock;
      break;
    case 'hide':
      Icon = Hide;
      break;
    case 'show':
      Icon = Show;
      break;
    default:
      return null;
  }
  return (
    <Icon
      className={combineClass}
      onClick={shape === 'hide' || shape === 'show' ? toggleVisible : null}
    />
  );
}

Icon.propTypes = {
  shape: string.isRequired
};
